html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  box-sizing: border-box;
}

body {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body div {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
}

body main {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/************************************Header******************************/
#header {
  height: 18% !important;
}

#header div {
  height: 54%;
}

#header img {
  position: fixed;
  width: 6.5rem;
  margin-left: 2%;
  cursor: pointer;
  -webkit-filter: drop-shadow(3px 3px 4px #3B4856);
          filter: drop-shadow(3px 3px 4px #3B4856);
  z-index: 6;
}

.demo-link {
  font-size: 1.5rem;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  text-align: center;
  text-decoration: underline;
}

.title {
  text-align: center;
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  /* font-family: "Arial Narrow", Arial, sans-serif; */
  letter-spacing: 0.05em;
  margin: 0;
  z-index: 4;
  position: relative;
}

.title-white {
  text-align: center;
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  /* font-family: "Arial Narrow", Arial, sans-serif; */
  letter-spacing: 0.05em;
  color: white;
  margin: 0;
  z-index: 4;
  position: relative;
}

/************************************Background******************************/
.nav {
  height: auto;
}

ul {
  list-style-type: none;
  position: fixed;
  width: 100%;
  top: 0;
  height: 4em!important;
  background-color: white;
  right: 0;
  height: auto;
  z-index: 5;
}

li {
  float: right;
}

li p {
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  font-size: 1.25rem;
  padding: 1em 3em 0 0;
  color: black;
  text-align: center;
  text-decoration: none;
}

.dropbtn {
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  font-size: 1.25rem;
  padding: 1em 3em 0 0;
  color: black;
  text-align: center;
  background: none;
  border: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  font-size: 1rem;
  padding: 1em 2em 1em 1em;
  text-decoration: none;
  display: block;
}

#drop-capitalsphere a {
  background-color: #35658c;
}

#drop-medalarm a {
  background-color: #A8EDDD;
}

#drop-litonail a {
  background-color: #BFD6D9;
}

#drop-familyhub a {
  background-color: #f7d865;
}

#drop-about a {
  background-color: #f7f78c;
}

.dropdown-content a:hover {
  /* background-color: #ddd; */
  text-decoration: underline;
}

.dropdown:hover .dropdown-content {display: block;}


