.pg-contact {
  background: #FFC78B;
  height: 100%;
}

.pg-contact div {
  height: auto;
}

.col-lg-6 {
  min-height: 0;
}

.title-contact {
  /* margin-top: -1em; */
  font-size: 3.5rem;
  text-align: center;
}

.title-contact span {
  font-family: "Arial Narrow", Arial, sans-serif;
  font-size: 2.2rem;
  letter-spacing: 0.1em;
}

.contact {
  width: 70%;
  margin-left: 15%;
  margin-top: 6em;
}

.contact img {
  float: left;
  width: 6em;
}

.contact p {
  font-size: 2.5rem;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  margin-left: 3em;
  margin-top: 0.5em;
  color: #FFF7E9;
}

.thanks {
  font-size: 9rem;
  color: #f2b87b;
  letter-spacing: 0.04em;
  position: absolute;
  bottom: 0;
  left: 23%;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
}
