#takeaways > div {
  height: auto;
}

#takeaways h2 {  
  margin: 0;
  font-size: 2.5rem;
  text-align: center;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
}

#takeaways h3 {
  margin-bottom: 0.8em;
  font-size: 1.7rem;
  text-align: center;
  font-family: "Arial Narrow", Arial, sans-serif;
  letter-spacing: 0.08em;
}

#takeaways h4 {
  margin-bottom: 1.5em;
  width: 70%;
  margin-left: 15%;
  font-size: 1.5rem;
  text-align: left;
  font-family: "Arial Narrow", Arial, sans-serif;
  letter-spacing: 0.08em;
  line-height: 1.2em;
  text-indent: 1em;
}

#takeaways p {

  text-indent: 1em;
  text-align: left;
  font-size: 1.4rem;
  letter-spacing: 0.05em;
  font-family: "Arial Narrow", Arial, sans-serif;
}

#takeaways > img {
  width: 60%;
  margin-left: 20%;
}

.up + div div {
  margin-bottom: 2em;
}

