/************************************Header******************************/
#header {
  height: 18% !important;
}

#header div {
  height: 54%;
}

#header img {
  position: fixed;
  width: 6.5rem;
  margin-left: 2%;
  cursor: pointer;
  filter: drop-shadow(3px 3px 4px #3B4856);
  z-index: 6;
}

.demo-link {
  font-size: 1.5rem;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  text-align: center;
  text-decoration: underline;
}

.title {
  text-align: center;
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  /* font-family: "Arial Narrow", Arial, sans-serif; */
  letter-spacing: 0.05em;
  margin: 0;
  z-index: 4;
  position: relative;
}

.title-white {
  text-align: center;
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  /* font-family: "Arial Narrow", Arial, sans-serif; */
  letter-spacing: 0.05em;
  color: white;
  margin: 0;
  z-index: 4;
  position: relative;
}

/************************************Background******************************/
.nav {
  height: auto;
}

ul {
  list-style-type: none;
  position: fixed;
  width: 100%;
  top: 0;
  height: 4em!important;
  background-color: white;
  right: 0;
  height: auto;
  z-index: 5;
}

li {
  float: right;
}

li p {
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  font-size: 1.25rem;
  padding: 1em 3em 0 0;
  color: black;
  text-align: center;
  text-decoration: none;
}

.dropbtn {
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  font-size: 1.25rem;
  padding: 1em 3em 0 0;
  color: black;
  text-align: center;
  background: none;
  border: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  font-size: 1rem;
  padding: 1em 2em 1em 1em;
  text-decoration: none;
  display: block;
}

#drop-capitalsphere a {
  background-color: #35658c;
}

#drop-medalarm a {
  background-color: #A8EDDD;
}

#drop-litonail a {
  background-color: #BFD6D9;
}

#drop-familyhub a {
  background-color: #f7d865;
}

#drop-about a {
  background-color: #f7f78c;
}

.dropdown-content a:hover {
  /* background-color: #ddd; */
  text-decoration: underline;
}

.dropdown:hover .dropdown-content {display: block;}


/************************************Background******************************/

.capitalsphere-bg {
  background-color: #457CA9;
}

.capitalsphere-bg p {
  color: #E6F4F1;
}

.familyhub-bg {
  background-color: #FCDC84;
}

.familyhub-bg p {
  color: #4C4637;
}

.litonail-bg {
  background-color: #E6F4F1;
}

.medalarm-bg {
  background-color: #D5F4FF;
}

.medalarm-bg p, h5 {
  color: #37535C;
}


/*****************************Projects***********************************/
.project-selected {
  cursor: pointer;
  width: 90%;
  margin-left: 5%;
  margin-top: -30px;
  filter: drop-shadow(10px 8px 4px #444444);
}

.project {
  width: 80%;
  margin-left: 10%;
  filter: drop-shadow(10px 8px 4px #3B4856);
}


/*****************************Project Guide***********************************/

.default p {
  text-align: center;
  font-size: 1.75rem;
  font-family: "Arial Narrow", Arial, sans-serif;
  letter-spacing: 0.1em;
  padding: 0.5em 0 2em 0;
}

.guide > p {
  text-align: center;
  font-size: 2rem;
  font-weight: 600;
  font-family: "Arial Narrow", Arial, sans-serif;
  letter-spacing: 0.05em;
  padding: 0.7em 0 0 0;
}

.links {
  text-align: center;
  font-size: 2rem;
  font-family: "Arial Narrow", Arial, sans-serif;
  letter-spacing: 0.05em;
}

.learn-more {
  width: 24%;
  margin-left: 38%;
  margin-bottom: 100px;
}

.learn-more button {
  border: 4px solid black;
  background: transparent;
  border-radius: 0.2rem;
  font-size: 1.125rem;
  font-family: "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", Arial, sans-serif;
  width: 45%;
  padding: 15px 0;
  color: black;
  letter-spacing: 0.1em;
}

/******************************DemoMobile*********************************/

.demo-mobile {
  width: 24%;
  position: absolute;
  top: 13%;
  left: 11%;
  padding-top: 202.9%;
}

.iphone {
  width: 100%;
  height: 24%;
  top: 0;
  left: 0;
  position: absolute;
  background: center no-repeat;
  background-size: cover;
  background-image: url('../img/iphone.png');
}

.demo-screen {
  position: absolute;
  top: 13%;
  left: 7%;
  overflow-y: scroll;
  width: 100%;
  padding-top: 52%;
  height: 75.3%;
}

.demo-screen img {
  top: 0;
  width: 86%;
  position: absolute;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;   /*optional: just make scrollbar invisible */
}

/******************************DemoDesktop*********************************/

.demo-desktop {
  width: 50%;
  position: absolute;
  top: 40%;
  left: 1%;
  padding-top: 56.25%;
}

.desktop {
  width: 100%;
  height: 52%;
  top: 0;
  left: 0;
  position: absolute;
  background: center no-repeat;
  background-size: cover;
  background-image: url('../img/desktop2.png');
}

.demo-screen-desktop {
  position: absolute;
  top: 5.7%;
  left: 5%;
  overflow-y: scroll;
  width: 100%;
  padding-top: 47%;
  height: 80%;
}

.demo-screen-desktop img {
  top: 0;
  width: 90%;
  position: absolute;
}

