/******************************TakeawaysMobile*********************************/

.takeaway-mobile {
  padding-bottom: 3em;
  width: 70%!important;
  margin-left: 15%;
}


.takeaway-mobile h6 {
  font-size: 1.1rem;
  margin-left: 12%;
  font-weight: 600;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
}

.progress-icon {
  width: 10%;
  float: left;
}

.progress-icon ~ img {
  width: 80%;
  margin-left: 10%;
}

.text p {
  text-align: center;
}

.text > div {
  margin-bottom: 1.5em;
}

.progress-icon-text {
  width: 3.5%;
  float: left;

}

.up button {
  border: none;
  font-size: 3.5rem;
  width: 12%;
  margin-left: 44%;
  background-color: transparent;
}

.up p {
  text-align: center;
  font-weight: 600;
  font-size: 1.2rem;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
}

/******************************TakeawaysDesktop********************************/
.takeaway-desktop {
  width: 80%!important;
  margin-left: 10%;
}

.takeaway-desktop h6 {
  font-size: 1.1rem;
  margin-left: 8%;
  font-weight: 600;
  font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
}

.progress-icon-desktop {
  width: 6%;
  float: left;
}

.progress-icon-desktop ~ img {
  width: 95%;
}

.progress-icon-desktop ~ div img {
  width: 95%;
  top: 0;
  position: absolute;
}

.progress-icon-desktop ~ div {
  position: absolute;
  width: 100%;
  padding-top: 55%;
  overflow: scroll;
}

.text-desktop {
  margin-top: 25%;
}

/******************************Sidebar********************************/
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  padding: 8em 0 0 2%;
  width: 11em;
  height: 100%;
  z-index: 0;
  /* border-right: 1px solid black; */
}

#capitalsphere-side {
  background-color: #35658c;
}

#medalarm-side {
