.pg-about {
  background: #FFFF99;
}

.about {
  width: 85%;
  margin-left: 15%;
  margin-top: 2%;
  /* height: 73% !important; */
}

.shadow {
  position: absolute;
  top: 14%;
  left: 13%;
  height: 75%;
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
  -moz-box-shadow: rgba(0, 0, 0, 0.8) 3px 3px 10px inset;
  -webkit-box-shadow: rgba(0, 0, 0, 0.8) 3px 3px 10px inset;
  box-shadow: rgba(0, 0, 0, 0.8) 3px 3px 10px inset;
  width: 24%;
  /* -webkit-transition: box-shadow 0.2s ease-in;
  -moz-transition: box-shadow 0.2s ease-in;
  transition: box-shadow 0.2s ease-in; */
}

.shadow img {
  top: 0;
  left: 0;
  padding: 3%;
  position: absolute;
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
  width: 100%;
}

.self-intro {
  height: auto;
  margin-top: 3em;
  width: 56%;
  margin-left: 42%;
}

.self-intro div {
  margin-top: 1em;
}

.self-intro img {
  width: 95%;
}

.self-intro p {
  font-size: 1.5rem;

  font-weight: 600;
  letter-spacing: 0.05em;
  line-height: 1.5em;
  font-family: "Arial Narrow", Arial, sans-serif;
}

.txt {
  width: 100%;
  padding-top: 13em;
  background-color: #FFFF99;
  height: auto;
}

.txt p {
  width: 70%;
  margin: 0;
  margin-left: 15%;
  text-indent: 1.2em;
  font-size: 1.6rem;
  letter-spacing: 0.03em;
  line-height: 1.5em;
  font-family: "Arial Narrow", Arial, sans-serif;
}

.resume {
  height: auto;
  background: #FFFF99;
  padding-top:50px
}

#resume {
  position: relative;
  -moz-box-shadow: rgba(0, 0, 0, 0.8) 3px 3px 10px inset;
  -webkit-box-shadow: rgba(0, 0, 0, 0.8) 3px 3px 10px inset;
  box-shadow: rgba(0, 0, 0, 0.8) 3px 3px 10px inset;
  width: 60%;
  margin-left: 20%;
  padding: 10px;
}

embed {
  height: 600px;
  width: 100%;
}
